/** Media Queries **/
.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header__heading {
  font-weight: bold;
  text-transform: lowercase;
}

.header__img {
  cursor: pointer;
}

@media (max-width: 1024px) {
  .header__nav {
    display: none;
  }

  .header__btns {
    display: none;
  }
}
@media (min-width: 1024px) {
  .header {
    display: grid;
    grid-template-columns: repeat(20, 1fr);
    grid-template-rows: repeat(3, 1fr);
    align-items: center;
  }
  .header__img {
    display: none;
  }
  .header__link {
    width: 13rem;
  }
  .header__link img {
    display: inline-block;
    margin-left: 2rem;
  }
  .header__nav {
    grid-column: 3/10;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    gap: 2rem;
  }
}
@media (min-width: 1024px) and (min-width: 1568px) {
  .header__nav {
    margin-bottom: 0;
  }
}
@media (min-width: 1024px) {
  .header__nav a {
    color: #585757;
    transition: color 0.5s;
  }
  .header__nav a:hover {
    color: #000;
  }
}
@media (min-width: 1024px) {
  .header__btns {
    grid-column: 15/19;
    display: flex;
    justify-content: space-evenly;
    gap: 8rem;
    color: #585757;
  }
}
@media (min-width: 1024px) {
  .header__btn {
    cursor: pointer;
    transition: color 0.5s;
  }
  .header__btn:hover {
    color: #000;
  }
}
@media (min-width: 1024px) {
  .header__btn:nth-child(2) {
    border: 2px solid #585757;
    padding: 1.5rem 3rem;
    border-radius: 1.5rem;
  }
  .header__btn:nth-child(2):hover {
    border: 2px solid #000;
  }
}
@media (min-width: 1024px) {
  .header__list {
    animation: growDown 300ms ease-in-out forwards;
    transform-origin: top center;
    color: #585757;
    grid-column: 2/4;
    grid-row: 2/4;
    width: 17rem;
    height: 100%;
    box-shadow: 0 3px 10px rgba(0, 0, 0, 0.2);
    border-radius: 1rem;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .header__list li {
    padding: 1rem 1rem;
  }
  .header__list li a {
    color: #585757;
  }
  .header__list li:nth-child(1):before {
    content: "";
    display: inline-block;
    width: 2rem;
    height: 2rem;
    background-image: url("../img/icon-todo.svg");
    background-repeat: no-repeat;
    position: relative;
    left: -1rem;
    bottom: -0.5rem;
  }
  .header__list li:nth-child(2):before {
    content: "";
    display: inline-block;
    width: 2rem;
    height: 2rem;
    background-image: url("../img/icon-calendar.svg");
    background-repeat: no-repeat;
    position: relative;
    left: -1rem;
    bottom: -0.5rem;
  }
  .header__list li:nth-child(3):before {
    content: "";
    display: inline-block;
    width: 2rem;
    height: 2rem;
    background-image: url("../img/icon-reminders.svg");
    background-repeat: no-repeat;
    position: relative;
    left: -1rem;
    bottom: -0.5rem;
  }
  .header__list li:nth-child(4):before {
    content: "";
    display: inline-block;
    width: 2rem;
    height: 2rem;
    background-image: url("../img/icon-planning.svg");
    background-repeat: no-repeat;
    position: relative;
    left: -1rem;
    bottom: -0.5rem;
  }
}
@media (min-width: 1024px) {
  .header__list2 {
    animation: growDown 300ms ease-in-out forwards;
    transform-origin: top center;
    color: #585757;
    grid-column: 5/7;
    grid-row: 2/4;
    width: 13rem;
    height: 80%;
    align-self: flex-start;
    box-shadow: 0 3px 10px rgba(0, 0, 0, 0.2);
    border-radius: 1rem;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .header__list2 li {
    padding: 1rem 0 1rem 2rem;
  }
  .header__list2 li a {
    color: #585757;
  }
}
@keyframes growDown {
  0% {
    transform: scaleY(0);
  }
  80% {
    transform: scaleY(1.1);
  }
  100% {
    transform: scaleY(1);
  }
}
.main {
  padding-bottom: 10rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.main__title {
  font-weight: 700;
  text-align: center;
}
.main__text {
  text-align: center;
  font-size: 2rem;
  color: #585757;
}
.main__btn {
  text-align: center;
  margin-top: 5rem;
}
.main__btn-extra {
  background-color: #000;
  padding: 3rem 5rem;
  font-size: 2rem;
  border-radius: 2rem;
  font-weight: 700;
  color: #FFFFFF;
  border: 1px solid #000;
}
.main__btn-extra:hover {
  transition: all 0.5s;
  background-color: #FFFFFF;
  color: #000;
  cursor: pointer;
}
@media (min-width: 1024px) {
  .main {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(2, 1fr);
    margin-top: 5rem;
  }
  .main__desc {
    grid-column: 1/2;
    grid-row: 1/2;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: 70rem;
  }
  .main__desc span {
    display: block;
  }
  .main__title {
    text-align: left;
    font-size: 10rem;
    line-height: 10rem;
    margin-bottom: 5rem;
  }
  .main__text {
    text-align: left;
    margin: 0;
  }
  .main__text--cont {
    width: 50rem;
  }
  .main__img {
    grid-column: 2/3;
  }
}
.main__icons {
  display: flex;
  gap: 4rem;
  justify-content: center;
  margin-bottom: 5rem;
}
@media (min-width: 1024px) {
  .main__icons {
    margin-bottom: 0;
  }
}

