html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Epilogue", sans-serif;
  font-size: 1.8rem;
  font-weight: 300;
}

h1, h2, h3, h4, h5, li {
  font-family: "Epilogue", sans-serif;
}

.container {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

img {
  max-width: 100%;
  display: block;
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

.hidden {
  display: none;
  overflow: hidden;
}

.mg-top-5 {
  margin-top: 5rem;
}

.mg-top-10 {
  margin-top: 10rem;
}

.textCenter {
  text-align: center;
}

.containerXl {
  width: 95%;
  max-width: 1800px;
  margin: 0 auto;
}

button {
  all: unset;
}

h1, h2, h3 {
  margin: 0;
  line-height: 1.2;
}

h1, h2 {
  font-weight: 400;
}

h1 {
  font-size: 4.4rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 2.4rem;
  font-weight: 300;
}

[class$=heading] {
  margin: 2rem 0;
}

a {
  text-decoration: none;
  color: #000;
}

p {
  font-size: 1.6rem;
  line-height: 1.8;
}

.modal__phone {
  animation: growLeft 300ms ease-in-out forwards;
  transform-origin: right center;
  position: fixed;
  background-color: #FFFFFF;
  width: 100%;
  height: 100%;
  overflow: auto;
  left: 30%;
  top: 0;
  box-shadow: 0 10rem 10rem rgba(0, 0, 0, 0.486);
}
.modal__phone--close {
  margin-left: 55%;
  margin-top: 2rem;
  cursor: pointer;
}

.modal__list {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  align-items: flex-start;
  margin-top: 5rem;
  padding-left: 5rem;
}
.modal__list a {
  color: #585757;
}
.modal__list a:hover {
  transition: color 1s;
  color: #000;
}
.modal__list--img {
  position: relative;
  top: -1.5rem;
  right: -9rem;
}

.modal__popup {
  display: flex;
  animation: growDown 300ms ease-in-out forwards;
  transform-origin: top center;
}
.modal__popup--icon {
  display: inline;
  padding-right: 1rem;
}
.modal__popup ul {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.modal__popup ul li {
  padding: 2rem 0 0 2rem;
}
.modal__popup ul li:first-of-type {
  padding-top: 0;
}
.modal__popup ul li a {
  text-transform: capitalize;
}

.modal__popup2 {
  display: flex;
  animation: growDown 300ms ease-in-out forwards;
  transform-origin: top center;
}
.modal__popup2--icon {
  display: inline;
  padding-right: 1rem;
}
.modal__popup2 ul {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.modal__popup2 ul li {
  padding: 2rem 0 0 2rem;
}
.modal__popup2 ul li:first-of-type {
  padding-top: 0;
}
.modal__popup2 ul li a {
  text-transform: capitalize;
}

@keyframes growDown {
  0% {
    transform: scaleY(0);
  }
  80% {
    transform: scaleY(1.1);
  }
  100% {
    transform: scaleY(1);
  }
}
@keyframes growLeft {
  0% {
    transform: scaleX(0);
  }
  80% {
    transform: scaleX(1.1);
  }
  100% {
    transform: scaleX(1);
  }
}
.hiddenModal {
  display: none;
}